.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
}

.main-content {
  flex: 1;
  padding: 20px 0;
  width: 100%;
}

@media (max-width: 768px) {
  .main-content {
    padding: 15px 0;
  }
}

@media (max-width: 480px) {
  .main-content {
    padding: 10px 0;
  }
} 