.dashboard-container {
  padding: 4rem 0;
  min-height: calc(100vh - 80px);
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
}

.dashboard-header {
  text-align: center;
  margin-bottom: 4rem;
}

.animated-title {
  font-size: 3.5rem;
  color: #2c3e50;
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  perspective: 1000px;
}

.title-word {
  display: inline-flex;
  position: relative;
  transition: all 0.3s ease;
}

.title-word:hover {
  transform: translateY(-3px);
  color: #3498db;
  text-shadow: 0 4px 8px rgba(52, 152, 219, 0.3);
}

.title-word::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 3px;
  background: #3498db;
  transition: width 0.3s ease;
}

.title-word:hover::after {
  width: 100%;
}

.user-welcome {
  font-size: 1.2rem;
  color: #7f8c8d;
  margin-bottom: 2rem;
  animation: fadeIn 0.5s ease;
}

.dashboard-content {
  display: grid;
  gap: 3rem;
}

.progress-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.progress-card {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.progress-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

.progress-card i {
  font-size: 2.5rem;
  color: #3498db;
  margin-bottom: 1rem;
}

.progress-card h3 {
  color: #2c3e50;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.progress-card p {
  color: #7f8c8d;
  font-size: 1.5rem;
  font-weight: 600;
}

.current-courses, .achievements {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.current-courses h2, .achievements h2 {
  color: #2c3e50;
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.course-card {
  background: #f8f9fa;
  padding: 2rem;
  border-radius: 12px;
  position: relative;
  transition: all 0.3s ease;
}

.course-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.course-progress {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: #3498db;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: 600;
}

.course-card h3 {
  color: #2c3e50;
  margin: 1rem 0;
  font-size: 1.3rem;
}

.course-card p {
  color: #7f8c8d;
  margin-bottom: 1.5rem;
}

.btn-continue {
  background: #3498db;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  width: 100%;
}

.btn-continue:hover {
  background: #2980b9;
  transform: translateY(-2px);
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.achievement-card {
  text-align: center;
  padding: 2rem;
  border-radius: 12px;
  background: #f8f9fa;
  transition: all 0.3s ease;
}

.achievement-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.achievement-card i {
  font-size: 2.5rem;
  color: #3498db;
  margin-bottom: 1rem;
}

.achievement-card.locked i {
  color: #95a5a6;
}

.achievement-card h3 {
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.achievement-card p {
  color: #7f8c8d;
}

.achievement-card.locked {
  opacity: 0.7;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 992px) {
  .dashboard-container {
    padding: 3rem 0;
  }
  
  .dashboard-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1.5rem;
    margin-bottom: 2.5rem;
  }
  
  .dashboard-greeting {
    font-size: 2.2rem;
  }
  
  .dashboard-actions {
    justify-content: center;
  }
  
  .dashboard-layout {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .dashboard-sidebar {
    position: static;
    width: 100%;
    padding: 1.5rem;
  }
  
  .dashboard-main {
    margin-left: 0;
  }
  
  .stat-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  
  .course-progress-list {
    gap: 1.5rem;
  }
}

@media (max-width: 768px) {
  .dashboard-container {
    padding: 2.5rem 0;
  }
  
  .dashboard-greeting {
    font-size: 1.8rem;
  }
  
  .dashboard-subtext {
    font-size: 1rem;
  }
  
  .action-button {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }
  
  .stat-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .stat-card {
    padding: 1.2rem;
  }
  
  .stat-value {
    font-size: 2rem;
  }
  
  .stat-label {
    font-size: 0.9rem;
  }
  
  .section-title {
    font-size: 1.5rem;
    margin-bottom: 1.2rem;
  }
  
  .progress-card {
    padding: 1.2rem;
  }
  
  .course-title {
    font-size: 1.1rem;
  }
  
  .progress-details {
    font-size: 0.9rem;
  }
  
  .progress-bar {
    height: 8px;
  }
  
  .nav-item a {
    padding: 0.8rem 1rem;
    font-size: 0.9rem;
  }
  
  .dashboard-menu {
    margin-bottom: 1.5rem;
  }
  
  .user-avatar {
    width: 70px;
    height: 70px;
  }
  
  .user-name {
    font-size: 1.3rem;
  }
  
  .user-email {
    font-size: 0.9rem;
  }
  
  .profile-link {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }
  
  .certificate-card {
    padding: 1.2rem;
  }
  
  .certificate-course {
    font-size: 1.1rem;
  }
  
  .certificate-date {
    font-size: 0.9rem;
  }
  
  .certificate-buttons {
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .certificate-button {
    width: 100%;
    padding: 0.7rem 1rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .dashboard-container {
    padding: 2rem 0;
  }
  
  .dashboard-greeting {
    font-size: 1.5rem;
  }
  
  .dashboard-subtext {
    font-size: 0.9rem;
  }
  
  .action-button {
    padding: 0.6rem 1rem;
    font-size: 0.85rem;
  }
  
  .dashboard-actions {
    flex-wrap: wrap;
    gap: 0.8rem;
  }
  
  .stat-card {
    padding: 1rem;
  }
  
  .stat-value {
    font-size: 1.8rem;
  }
  
  .stat-label {
    font-size: 0.85rem;
  }
  
  .section-title {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }
  
  .progress-card {
    padding: 1rem;
  }
  
  .course-title {
    font-size: 1rem;
  }
  
  .progress-details {
    font-size: 0.85rem;
  }
  
  .progress-bar {
    height: 6px;
  }
  
  .action-btn {
    padding: 0.5rem 0.8rem;
    font-size: 0.85rem;
  }
  
  .user-info {
    padding: 1rem;
  }
  
  .user-avatar {
    width: 60px;
    height: 60px;
  }
  
  .user-name {
    font-size: 1.2rem;
  }
  
  .user-email {
    font-size: 0.85rem;
  }
  
  .nav-item a {
    padding: 0.7rem 0.8rem;
    font-size: 0.85rem;
  }
  
  .certificate-card {
    padding: 1rem;
  }
  
  .certificate-course {
    font-size: 1rem;
  }
  
  .certificate-date {
    font-size: 0.85rem;
  }
  
  .certificate-button {
    padding: 0.6rem 0.8rem;
    font-size: 0.85rem;
  }
}
